@charset "UTF-8";
.home-banner {
  position: relative;
  top: 104px;
  background-image: url(https://assets.website-files.com/5badda2935e11303a89a461e/5baddd4835e113c6299a48f5_li-tzuni-507346-unsplash.jpg);
  background-position: 50% 94%;
  background-size: cover;
  background-repeat: no-repeat;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.scroll-mouse-link {
  position: absolute;
  bottom: 0px;
  z-index: 2;
  display: flex;
  width: 64px;
  height: 64px;
  margin: 0 auto -32px auto;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.15);
}

.scroll-mouse-link .mouse-icon {
  width: 20px;
  height: 30px;
  border: 2px solid #bbb;
  border-radius: 12px;
  display: flex;
  justify-content: center;
}

.scroll-mouse-link .mouse-icon .mouse-wheel-icon {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #bbb;
  margin-top: 6px;
}

/* 淡入动画 */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.hero-intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 570px;
  width: 100%;
  background-color: #fff;
  padding: 56px 48px;
  border-radius: 16px;
  color: #111;
  text-align: center;
  box-sizing: border-box;
  animation: fadeIn 1s ease forwards;
}

.hero-intro .title {
  margin-bottom: 16px;
  color: #a5c926;
  font-size: 13px;
  line-height: 16px;
  font-weight: 600;
}

.hero-intro .w-button {
  height: 48px;
  padding: 12px 28px;
  border-radius: 24px;
  background-color: #a5c926;
  color: #fff;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  text-align: center;
  box-sizing: border-box;
  text-decoration: none;
  border: 0;
  display: inline-block;
  transition: transform 300ms ease, box-shadow 300ms ease, -webkit-transform 300ms ease;
}

.hero-intro .w-button:hover {
  box-shadow: 0 8px 8px -4px rgba(165, 201, 38, 0.5);
  -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05);
}

.home-categories {
  display: flex;
  margin-top: 150px;
  width: 100%;
}

.home-categories img {
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}

.home-categories .home-category-card {
  border-radius: 16px;
  background-color: #ffc12c;
  color: #fff;
  margin-right: 15px;
  margin-left: 15px;
  padding: 48px 40px;
  position: relative;
  display: flex;
  flex: 1;
  animation: fadeIn 1s ease forwards;
}

.home-categories .home-category-card .home-category-image-1 {
  position: absolute;
  left: 15px;
  bottom: 26px;
  width: 220px;
}

.home-categories .home-category-card .home-category-image-2 {
  position: absolute;
  right: 21px;
  bottom: 17px;
  width: 220px;
}

.home-categories .home-category-card .home-category-info-1 {
  margin-left: 224px;
}

.home-categories .home-category-card .home-category-info-2 {
  margin-right: 224px;
  text-align: right;
}

.home-categories .home-category-card .w-button {
  background-color: #fff;
  color: #111;
  height: 40px;
  padding: 8px 24px;
  border-radius: 20px;
  font-size: 13px;
  line-height: 24px;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  transition: transform 300ms ease, box-shadow 300ms ease;
}

.home-categories .home-category-card .w-button:hover {
  box-shadow: 0 8px 8px -4px rgba(17, 17, 17, 0.2);
  transform: scale(1.25);
}

.home-categories .red-card {
  background-color: #fb416b;
}

